import React, {Component} from "react";
import PropTypes from "prop-types";

import OraSwitcherSelect from "eshop/modules/core/components/ui/OraSwitcherSelect";
import {ConditionalInfoComponent} from "../../../components/InfoComponent";
import OfferType from "../../core/constants/OfferTypeEnum";
import SalesChannel from "../../core/enum/SalesChannel";
import SoftBundleGroup from "../../core/enum/SoftBundleGroup";


export default class OfferFilterComponentsSwitcherView extends Component {

    renderConditionalInfo = ({channels, offerType, softBundleGroup, hasLove}) => {
        if (channels.sales === SalesChannel.WWW &&
            offerType === OfferType.VOICE_LDF &&
            softBundleGroup === SoftBundleGroup.LDF) {
            return (
                <ConditionalInfoComponent
                    condition={hasLove}
                    type={"blueInfo"}
                    color={"black"}
                    bgColor={"white"}
                    title={"Pamiętaj!"}
                    padding={"noPadding"}
                    text={"Jesteś w Love dla Firm - dzięki temu korzystasz ze specjalnych rabatów. Jeśli chcesz korzystać z nich dalej - musisz mieć aktywną umowę na internet dla biura."}
                    altText={"Love dla firm to pakiet obejmujący abonament komórkowy i internet stacjonarny w Orange dzięki czemu koszt pierwszej karty jest od 35,00 zł + VAT, a drugą kartę dostaniesz za 0 zł."}>
                </ConditionalInfoComponent>
            );
        }
    };
    render() {

        if (!(!!this.props.offerType)) {
            return null
        }

        return (
            <div className="l-full-row g-gray1-bg">
                <div className="l-page-row u-padding-l ">
                    <div className={" "+(this.props.selectedOfferType===OfferType.DATA ? " u-border-top":" u-padding-m u-padding-top")}>
                        {
                            this.props.descriptions.processSelectHeader && this.props.offerType===OfferType.DATA &&
                            <h2>{this.props.descriptions.processSelectHeader}</h2>
                        }

                        {/*desktop view*/}
                        <div className="u-medium-hide u-small-hide u-padding-top-s">
                            <OraSwitcherSelect
                                key={"processTypeFilter"}
                                options={this.props.processTypeData}
                                disabledOptions={this.props.disabledProcesses}
                                id="processTypeFilter"
                                selected={this.props.selectedProcessValue}
                                onChange={this.props.selectProcessCallback}
                                className="u-small-left u-left u-w-100 u-small-w-auto u-medium-w-100 u-small-w-100"
                                isFeedbackParam="true"
                                selectClassName="opl-input--size-m"
                            />
                        </div>

                        {/*tablet and smartphone view - */}
                        <div className="u-large-hide">
                            <OraSwitcherSelect
                                key={"processTypeFilter"}
                                options={this.props.processTypeData}
                                disabledOptions={this.props.disabledProcesses}
                                id="processTypeFilter"
                                selected={this.props.selectedProcessValue}
                                onChange={this.props.selectProcessCallback}
                                className="u-small-left u-left u-w-100 u-small-w-auto u-medium-w-100 u-small-w-100"
                                isFeedbackParam="true"
                                selectClassName="opl-input--size-m"
                            />
                        </div>

                    </div>
                    {
                        this.renderConditionalInfo(this.props)
                    }
                    {this.props.selectedProcessValue && [
                        <LoyaltySelectHeader key={"loyaltySelectHeader"} {...this.props.descriptions}/>,
                        <LoyaltySelectDiv key={"loyaltySelectDiv"} {...this.props}/>,
                    ]
                    }
                </div>
            </div>
        );
    }
}
const propTypes = {
    selectedProcessValue: PropTypes.string,
    processTypeData: PropTypes.arrayOf(PropTypes.shape({
        value: PropTypes.string,
        description: PropTypes.string,
    })),
    selectProcessCallback: PropTypes.func,
    selectedLoyaltyLengthValue: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number,
    ]),
    loyaltyLengthData: PropTypes.arrayOf(PropTypes.shape({
        value: PropTypes.oneOfType([
            PropTypes.string,
            PropTypes.number,
        ]),
        description: PropTypes.string,
    })),
    selectLoyaltyLengthCallback: PropTypes.func,
    softBundleGroup: PropTypes.string,
    contractRole: PropTypes.string,
    offerType: PropTypes.string,
    channels: PropTypes.object,
    hasLove: PropTypes.bool,
    hasODF: PropTypes.bool,
    descriptions: PropTypes.objectOf(PropTypes.string),
    selectedOfferType: PropTypes.objectOf(OfferType),
};
OfferFilterComponentsSwitcherView.propTypes = propTypes;

const LoyaltySelectHeader = ({loyaltySelectHeader}) =>
    (<div className={"u-border-top u-no-margin u-padding-l u-padding-top-m"}>
        {
            loyaltySelectHeader &&
            <h3>{loyaltySelectHeader}</h3>
        }
    </div>);
LoyaltySelectHeader.propTypes = {
    loyaltySelectHeader: PropTypes.string,
};
const LoyaltySelectDiv = ({loyaltyLengthData,selectedLoyaltyLengthValue,selectLoyaltyLengthCallback}) => (
    <OraSwitcherSelect
        key={"loyaltyFilter"}
        options={loyaltyLengthData}
        id="loyaltyFilter"
        selected={selectedLoyaltyLengthValue}
        onChange={selectLoyaltyLengthCallback}
        className="u-small-left u-left u-w-100 u-small-w-auto u-medium-w-100 u-small-w-100"
        selectClassName="opl-input--size-m"
        singleOptionClassName="u-small-no-padding-left"
        renderSingleOption={true}
    />
);
LoyaltySelectDiv.propTypes = {
    loyaltyLengthData: PropTypes.array,
    selectedLoyaltyLengthValue: PropTypes.number,
    selectLoyaltyLengthCallback: PropTypes.func.isRequired,
}